        color: #fff;
    }
    .kg-bookmark-description {
        color: rgba(255, 255, 255, 0.75);
    }
    .kg-bookmark-metadata {
        color: rgba(255, 255, 255, 0.75);
    }
    .site-archive-header .no-image {
        color: rgba(255, 255, 255, 0.9);
        background: var(--darkmode);
    }
    .site-archive-header .no-image .site-header-content {
        border-bottom-color: color(var(--darkmode) l(+15%));
    }
    .site-header-content .author-profile-image {
        box-shadow: 0 0 0 6px hsla(0, 0%, 100%, 0.04);
    }
    .subscribe-form {
        border: none;
        background: linear-gradient(color(var(--darkmode) l(-6%)), color(var(--darkmode) l(-3%)));
    }
    .subscribe-form-title {
        color: rgba(255, 255, 255, 0.9);
    }
    .subscribe-form p {
        color: rgba(255, 255, 255, 0.7);
    }
    .subscribe-email {
        border-color: color(var(--darkmode) l(+6%));
        color: rgba(255, 255, 255, 0.9);
        background: color(var(--darkmode) l(+3%));
    }
    .subscribe-email:focus {
        border-color: color(var(--darkmode) l(+25%));
    }
    .subscribe-form button {
        opacity: 0.9;
    }
    .subscribe-form .invalid .message-error, .subscribe-form .error .message-error {
        color: color(var(--red) l(+5%) s(-5%));
    }
    .subscribe-form .success .message-success {
        color: color(var(--green) l(+5%) s(-5%));
    }
}

/* Custom Top Area */

.post-top-area {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 15px -15px;
    padding-bottom: 40px;
    border-bottom: 1px solid #efefef;
    margin-bottom: 25px;
}

.post-top-col {
    flex: 0 0 50%;
    max-width: 50%;
    min-height: 1px;
    padding: 0 15px;
}

@media (max-width: 767px) {
    .post-top-col {
        flex: 0 0 100%;
        max-width: 100%;
    }
    .post-top-col:first-child {
        padding-top: 30px;
        padding-bottom: 30px;
    }
}

/* Social Share Buttons */

.social-share {
    margin-top: 20px;
    padding: 0 10px;
}

.social-share ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.social-share ul li {
    display: inline-block;
    padding-left: 0;
    padding-right: 15px;
    margin: 0;
}

.social-share ul li a {
    color: #b1b1b1;
    font-size: 20px;
}

/* End */

.breathing {
    width: 270px;
    padding: 20px;
    margin: 50px auto;
    border: 1px solid #d1d1d1;
    animation: breathing 2s ease-out infinite normal;
    font-size: 24px;
    background: #5885cb;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    border-radius: 3px;
    text-align: center;
}

.heart {
    width: 100px;
    height: 100px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url(https://cssanimation.rocks/images/posts/steps/heart.png) no-repeat;
    background-position: 0 0;
    cursor: pointer;
    animation: fave-heart 1s steps(28) infinite;
}

@keyframes fave-heart {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: -2800px 0;
    }
}

/* Embla starts */

.embla {
    position: relative;
}

.embla__viewport {
    width: 100%;
    overflow: hidden;
}

.embla__viewport.is-draggable {
    cursor: move;
    cursor: grab;
}

.embla__viewport.is-dragging {
    cursor: grabbing;
}

.embla__container {
    display: flex;
    will-change: transform;
    margin-left: -1rem;
}

.embla__slide {
    flex: 0 0 auto;
    width: 50%;
    position: relative;
    padding-left: 1rem;
    counter-increment: embla;
}

@media (max-width: 576px) {
    .embla__slide {
        width: 80%;
    }
}

.embla__slide__inner {
    background-color: rgb(40, 44, 52);
    position: relative;
    border-radius: 1.5rem;
    min-height: 325px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    overflow: hidden;
}

.embla__slide__inner:before {
    color: white;
    font-weight: 300;
    line-height: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.embla__slide__inner__content {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 20px 8px;
    width: 100%;
    height: auto;
    height: 100%;
    color: #fff;
    background: #fff;
    background: rgba(0, 0, 0, 0.4) 0%;
    display: flex;
    align-items: flex-end;
}

.embla__slide__inner__content .post-card-primary-tag, .embla__slide__inner__content .post-card-title {
    color: var(--white);
}

/* Slide Custom */

.post-card-primary-tag.slide {
    background: rgba(0, 0, 0, 0.5);
    padding: 2px 10px;
    border-radius: 4px;
    font-weight: 400;
    color: #fff;
}

.slide .post-card-title {
    color: #fff;
    font-size: 26px;
    line-height: 32px;
    font-weight: 400;
}

.post-sld-date {
    color: #fff;
    font-size: 11px;
    padding: 0px 18px;
}

.embla__button {
    background-color: #fff;
    border-radius: 100%;
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    border: 0;
    width: 5rem;
    height: 5rem;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.embla__button:focus {
    outline: 0;
    outline: none;
}

.embla__button:not(:disabled) {
    cursor: pointer;
    fill: var(--lightgrey);
}

.embla__button:disabled {
    fill: #e9e9e9;
}

.embla__button__svg {
    width: 20px;
    height: 20px;
}

.embla__button--prev {
    left: 0.7rem;
}

.embla__button--next {
    right: 0.7rem;
}

.embla__dots {
    position: absolute;
    margin-top: 1rem;
    display: flex;
    list-style: none;
    padding-left: 0;
    justify-content: center;
    left: 0;
    right: 0;
    top: 100%;
}

.embla__dot {
    background-color: transparent;
    cursor: pointer;
    position: relative;
    padding: 0;
    width: 3rem;
    height: 3rem;
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    border: 0;
    display: flex;
    align-items: center;
}

.embla__dot:after {
    background-color: #efefef;
    width: 100%;
    height: 0.4rem;
    content: "";
}

.embla__dot.is-selected:after {
    background-color: #1bcacd;
    opacity: 1;
}

/* Embla ends */

.text-center {
    text-align: center;
}

.author-post-count {
    background: rgba(0, 0, 0, 0.6);
    color: #fff;
    text-decoration-color: white;
    padding: .5rem;
    border-radius: 0 1rem 1rem 0;
    position: absolute;
    margin-top: 1rem;
}

.post-card-tags {
    display: block;
    margin-bottom: 4px;
    color: #666;
    font-size: 1.2rem;
    line-height: 1.15em;
    font-weight: 500;
    letter-spacing: .5px;
    text-transform: uppercase;
}

.pull-left {
    float: left;
}

/* category bar starts */

.category-holder {
